*,
*::before,
*::after {
  box-sizing: border-box;
}

html {
  height: 100%;
}

body {
  margin: 0;
  display: grid;
  min-height: 100%;
  grid-template-rows: min-content 1fr min-content;
  align-content: start;
  font-family: "Montserrat", sans-serif;
  font-weight: 400;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

.container {
  max-width: 100%;
  margin: 0 auto;
  padding: 0 rem(10px);
}
@media (min-width: 768px) {
  .container {
    padding: 0 rem(50px);
  }
}

.main-btn {
  display: block;
  padding: rem(16px) 0;
  background: #ff005c;
  box-shadow: rem(0 0 30px) rgba(0, 133, 255, 0.25);
  font-size: rem(15px);
  line-height: rem(18px);
  font-weight: 700;
  color: #ffffff;
  text-decoration: none;
  text-align: center;
}

.header__container {
  position: relative;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  padding: rem(24px) 0;
}

.header__sandwich {
  width: 25px;
  height: 15px;
  margin-right: rem(3px);
}

.main-nav {
  position: absolute;
  top: 73px;
  z-index: 100;
  display: none;
  padding: rem(50px) rem(10px) 0;
  background: #ffffff;
}
.main-nav--no-js {
  position: relative;
  top: 0;
  display: block;
}
.main-nav--opened {
  display: block;
}

.main-nav__heading {
  margin: 0 0 rem(30px);
  font-size: rem(18px);
  font-weight: 700;
  line-height: 135%;
  color: #000000;
}

.main-nav__list {
  display: flex;
  margin: 0;
  padding: 0;
  list-style-type: none;
}

.main-nav__link {
  text-decoration: none;
  font-size: rem(15px);
  font-weight: 500;
  line-height: 135%;
  color: #000000;
}

.main-nav__phone {
  margin-bottom: rem(25px);
}
.main-nav__phone a {
  display: block;
  margin-bottom: rem(5px);
  text-decoration: none;
  font-size: rem(18px);
  line-height: 165%;
  font-weight: 600;
  color: #ff005c;
}

.main-nav__schedule {
  margin: 0;
  font-size: rem(18px);
  line-height: 165%;
  font-weight: 500;
  color: #797979;
}

.main-nav__address {
  font-style: normal;
  margin-bottom: rem(35px);
}
.main-nav__address-city {
  display: block;
  font-size: rem(15px);
  font-weight: 500;
  line-height: 135%;
  color: #797979;
}
.main-nav__address-street {
  display: block;
  margin-bottom: rem(5px);
  font-size: rem(16px);
  line-height: 165%;
  font-weight: 600;
  color: #ff005c;
}
.main-nav__address-metro {
  display: block;
  font-size: rem(15px);
  font-weight: 500;
  line-height: 135%;
  color: #000000;
}

.main-nav__button {
  width: 65%;
}